import React, {useState} from 'react';
import HTMLReactParser from "html-react-parser";
import {useParams} from "react-router-dom";
import millify from 'millify'
import {Col, Row, Typography} from "antd";
import {
  MoneyCollectOutlined,
  DollarCircleOutlined,
  FundOutlined,
  ExclamationCircleOutlined,
  StopOutlined, ThunderboltOutlined, TrophyOutlined, CheckOutlined, NumberOutlined
} from "@ant-design/icons";
import {useGetCryptosDetailsQuery, useGetCryptosHistoryQuery} from "../services/cryptoApi";
import LineChart from "./LineChart";
import Loader from "./Loader";

const {Title, Text} = Typography

const CryptoDetail = () => {

  const {coinId} = useParams()
  const [timePeriod, setTimePeriod] = useState('7d');
  const {data, isFetching} = useGetCryptosDetailsQuery(coinId)
  const {data: coinHistory} = useGetCryptosHistoryQuery({coinId, timePeriod})
  const cryptoDetails = data?.data?.coin

  console.log(timePeriod)

  if(isFetching) return <Loader />

  const time = ['3h', '24h', '7d', '30d', '1y', '3m', '3y', '5y'];

  const stats = [
    {
      title: 'Price to USD',
      value: `$ ${cryptoDetails?.price && millify(cryptoDetails?.price)}`,
      icon: <DollarCircleOutlined/>
    },
    {title: 'Rank', value: cryptoDetails?.rank, icon: <NumberOutlined/>},
    {
      title: '24h Volume',
      value: `$ ${cryptoDetails?.volume && millify(cryptoDetails?.volume)}`,
      icon: <ThunderboltOutlined/>
    },
    {
      title: 'Market Cap',
      value: `$ ${cryptoDetails?.marketCap && millify(cryptoDetails?.marketCap)}`,
      icon: <DollarCircleOutlined/>
    },
    {
      title: 'All-time-high(daily avg.)',
      value: `$ ${cryptoDetails?.allTimeHigh?.price && millify(cryptoDetails?.allTimeHigh?.price)}`,
      icon: <TrophyOutlined/>
    },
  ];

  const genericStats = [
    {title: 'Number Of Markets', value: cryptoDetails?.numberOfMarkets, icon: <FundOutlined/>},
    {title: 'Number Of Exchanges', value: cryptoDetails?.numberOfExchanges, icon: <MoneyCollectOutlined/>},
    {
      title: 'Aprroved Supply',
      value: cryptoDetails?.supply?.confirmed ? <CheckOutlined/> : <StopOutlined/>,
      icon: <ExclamationCircleOutlined/>
    },
    {
      title: 'Total Supply',
      value: `$ ${cryptoDetails?.supply?.total && millify(cryptoDetails?.supply?.total)}`,
      icon: <ExclamationCircleOutlined/>
    },
    {
      title: 'Circulating Supply',
      value: `$ ${cryptoDetails?.supply?.circulating && millify(cryptoDetails?.supply?.circulating)}`,
      icon: <ExclamationCircleOutlined/>
    },
  ];

  return (
    <Col className="coin-detail-container">
      <Col className="coin-heading-container">
        <Title level={2} className="coin-name">{cryptoDetails.name} ({cryptoDetails.symbol}) Price</Title>
        <p>
          {cryptoDetails.name} live price in US dollars.
          View value statistics, market cap and supply
        </p>
      </Col>
      <select
        value={timePeriod}
        className="select-timeperiod"
        placeholder="Select Time Period"
        onChange={e => setTimePeriod(e.target.value)}
      >
        {time.map((option,i)=> <option key={i} value={option}>{option}</option>)}
      </select>
      <LineChart
        coinHistory={coinHistory}
        currentPrice={millify(cryptoDetails.price)}
        coinName={cryptoDetails.name}
      />
      <Row gutter={[24,12]} justify={"space-around"}>
        <Col className="coin-value-statistics">
          <Col className="coin-value-statistics-heading">
            <Title level={3} className="coin-detailes-heading">{cryptoDetails.name} Value Statistics</Title>
            <p>An overview showing the stats of {cryptoDetails.name}</p>
          </Col>
          {stats.map(({icon, title, value},i)=> (
            <Col key={i} className='coin-stats'>
              <Col className='coin-stats-name'>
                <Text>{icon}</Text>
                <Text>{title}</Text>
              </Col>
              <Text className="stats">{value}</Text>
            </Col>
          ))}
        </Col>
        <Col className="other-stats-info">
          <Col className="coin-value-statistics-heading">
            <Title level={3} className="coin-detailes-heading">Other Statistics</Title>
            <p>An overview showing the stats of all cryptocurrency</p>
          </Col>
          {genericStats.map(({icon, title, value},i)=> (
            <Col key={i} className='coin-stats'>
              <Col className='coin-stats-name'>
                <Text>{icon}</Text>
                <Text>{title}</Text>
              </Col>
              <Text className="stats">{value}</Text>
            </Col>
          ))}
        </Col>
      </Row>
      <Row gutter={[24,12]}>
        <Col lg={12} md={24} className="coin-desc-link">
          <Row>
            <Title level={3} className="coin-details-heading">
              What is {cryptoDetails.name}
            </Title>
            {HTMLReactParser(cryptoDetails.description)}
          </Row>
        </Col>
        <Col lg={12} md={24}  className="coin-links">
          <Title level={3} className={"coin-details-heading"} >{cryptoDetails.name} Links</Title>
          {cryptoDetails.links.map(link =>(
            <Row className={"coin-link"} key={link.url}>
              <Title level={5} className={"link-name"}>
                {link.type}
              </Title>
              <a href={link.url} target={"_blank"} rel={"noreferrer"}>{link.name}</a>
            </Row>
          ))}
        </Col>
      </Row>
    </Col>
  )
};

export default CryptoDetail;